
import React, { Component } from 'react';
import DayPicker from 'react-day-picker';
import 'react-day-picker/lib/style.css';

import MomentLocaleUtils from 'react-day-picker/moment';
import 'moment/locale/ru';
import moment from 'moment'

import './PeriodCalendar.css'
import '../ButtonsGroup/ButtonsGroup.css'


const prevMonth = moment(new Date()).subtract(1, 'months').toDate()

const dateFormat = date => moment(date).format("D MMMM YYYY");
const dateFormatSubmit = date => moment(new Date(date)).format('YYYY-MM-DD');

function PeriodLabel({ start, end, interval, onReset }) {

    const intervalLabel = {
        days: 'по дням',
        weeks: 'по неделям',
        months: 'по месяцам'
    }

    start = dateFormat(start).split(' ')
    end = dateFormat(end).split(' ')

    if (start[2] === end[2]) start.splice(2, 1)
    else return (<span className="bPeriod__labelText">
        Период c {start.join(' ')} по {end.join(' ')}. Разделить {intervalLabel[interval]}.
        <a className="bPeriod__setupLink" onClick={onReset}>Выбрать другой</a>
    </span>)

    if (start[1] === end[1]) start.splice(1, 1)
    else return (<span className="bPeriod__labelText">
        Период c {start.join(' ')} по {end.join(' ')}. Разделить {intervalLabel[interval]}.
        <a className="bPeriod__setupLink" onClick={onReset}>Выбрать другой</a>
    </span>)

    return (<span className="bPeriod__labelText">
        Период c {start.join(' ')} по {end.join(' ')}. Разделить {intervalLabel[interval]}.
        <a className="bPeriod__setupLink" onClick={onReset}>Выбрать другой</a>
    </span>)
}

export default class extends Component {
    static defaultProps = { numberOfMonths: 1 };

    constructor(props) {
        super(props);
        this.state = this.props.interval ?
            this.props.interval
            :
            Object.assign({}, this.resetState(), { animateClass: 'hidePeriod' });

        this.endDayClick = this.endDayClick.bind(this);
        this.startDayClick = this.startDayClick.bind(this);
        this.handleResetClick = this.handleResetClick.bind(this);
        this.handleSubmitClick = this.handleSubmitClick.bind(this);
        this.handleTypeClick = this.handleTypeClick.bind(this);

        console.log('PeriodCalendar', this.props.interval, this.state)
    }

    componentDidMount() {
        setTimeout(() => this.setState({ animateClass: 'showPeriod' }), 0)
    }

    resetState() {
        return {
            start: undefined,
            end: undefined,
            intervalType: undefined
        };
    }

    handleResetClick() {
        this.setState(this.resetState());
    }

    handleSubmitClick() {
        const { start, end, intervalType } = this.state;
        this.props.onInterval && this.props.onInterval({
            start: dateFormatSubmit(start),
            end: dateFormatSubmit(end),
            intervalType
        })
        window.scrollTo(0, 0)
    }

    endDayClick(day, { selected }) {
        this.setState({ end: selected ? undefined : day }, () => {
            const { start, end, intervalType } = this.state;
            if (!start || !end || !intervalType) return;
            this.handleSubmitClick()
        });
    }

    startDayClick(day, { selected }) {
        this.setState({ start: selected ? undefined : day }, () => {
            const { start, end, intervalType } = this.state;
            if (!start || !end || !intervalType) return;
            this.handleSubmitClick()
        });
    }

    handleTypeClick(type) {
        this.setState({ intervalType: type }, () => {
            const { start, end, intervalType } = this.state;
            if (!start || !end || !intervalType) return;
            this.handleSubmitClick()
        });
    }

    render() {
        const { start, end, intervalType, animateClass } = this.state;

        function isActiveInterval(selectedType, currentType) {
            if (currentType === selectedType) return 'bButtonsGroup__button--active'
            return ''
        }

        return (
            <div className={'bPeriodWrapper ' + animateClass}>
                <h4>
                    {(!start || !end || !intervalType) && 'Выберите даты начала и конца периода'}
                    {(start && end && intervalType) && <PeriodLabel
                        start={start} end={end} interval={intervalType}
                        onReset={this.handleResetClick} />}
                </h4>
                {(!start || !end || !intervalType) && <div className={(start && end && intervalType) ? 'hidePeriod' : 'showPeriod'}>
                    <DayPicker
                        month={prevMonth}
                        localeUtils={MomentLocaleUtils}
                        locale="ru"
                        className="bPeriod bPeriod__delimeter"
                        disabledDays={{ before: this.state.start, after: this.state.end }}
                        selectedDays={this.state.start}
                        onDayClick={this.startDayClick}
                    />
                    <DayPicker
                        disabled
                        className="bPeriod"
                        localeUtils={MomentLocaleUtils}
                        locale="ru"
                        disabledDays={{ before: this.state.start, after: this.state.end }}
                        selectedDays={this.state.end}
                        onDayClick={this.endDayClick}
                    />

                    <p style={{ padding: '.7rem 1.5rem', borderTop: '1px solid #eee', color: '#989898', paddingTop: '1.5rem' }}>
                        Выберите интервал
                    </p>
                    <div className="bButtonsGroup" style={{ padding: '0 1.5rem', paddingBottom: '1.5rem' }}>
                        <button className={'bButtonsGroup__button ' + isActiveInterval(intervalType, 'days')}
                            onClick={() => this.handleTypeClick('days')}>
                            Дни
                        </button>
                        <button className={'bButtonsGroup__button ' + isActiveInterval(intervalType, 'weeks')}
                            onClick={() => this.handleTypeClick('weeks')}>
                            Недели
                        </button>
                        <button className={'bButtonsGroup__button ' + isActiveInterval(intervalType, 'months')}
                            onClick={() => this.handleTypeClick('months')}>
                            Месяцы
                        </button>
                    </div>
                </div>}
            </div>
        );
    }
}


