import React, { Component } from 'react';
// import classNames from "classnames"

import DayPicker from 'react-day-picker';
import 'react-day-picker/lib/style.css';

import MomentLocaleUtils from 'react-day-picker/moment';
import 'moment/locale/ru';
import moment from 'moment'

// import './Calendar.css'
const dateFormatSubmit = date => moment(new Date(date)).format('YYYY-MM-DD');


export default class extends Component {
    state = { date: '', animateClass: '' }

    componentDidMount() {
        setTimeout(() => this.setState({ animateClass: 'showPeriod' }), 0)
    }

    dayClick(date) {
        console.log({
            start: dateFormatSubmit(date),
            end: dateFormatSubmit(date),
            intervalType: 'days'
        })
        this.props.onInterval && this.props.onInterval({
            start: dateFormatSubmit(date),
            end: dateFormatSubmit(date),
            intervalType: 'days'
        })
        window.scrollTo(0, 0)
    }

    render() {
        const { animateClass } = this.state
        return (
            <div className={'bPeriodWrapper ' + animateClass}>
                <h4>Выберите дату</h4>
                <DayPicker
                    localeUtils={MomentLocaleUtils}
                    locale="ru"
                    className="bPeriod bPeriod__delimeter"
                    selectedDays={this.state.date}
                    onDayClick={this.dayClick.bind(this)}
                />
            </div>
        )
    }
}


